"use client"

import { useState } from 'react'

export default function TradingCardPage() {
  const [playerName, setPlayerName] = useState('');
  const [team, setTeam] = useState('');
  const [position, setPosition] = useState('');
  const [season, setSeason] = useState('');
  const [jerseyNumber, setJerseyNumber] = useState('');
  const [achievement, setAchievement] = useState('');
  const [cardStyle, setCardStyle] = useState('modern');
  const [cardImage, setCardImage] = useState('');

  const handleGenerateCard = async () => {
    const response = await fetch('/api/generate-card', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ 
        playerName, 
        team, 
        position: position.split(' (')[0],
        season,
        jerseyNumber,
        achievement,
        cardStyle
      }),
    });
    const data = await response.json();
    setCardImage(data.imageUrl);
  };

  const positions = [
    'Quarterback (QB)',
    'Running Back (RB)',
    'Wide Receiver (WR)',
    'Tight End (TE)',
    'Offensive Tackle (OT)',
    'Guard (G)',
    'Center (C)',
    'Defensive End (DE)',
    'Defensive Tackle (DT)',
    'Linebacker (LB)',
    'Cornerback (CB)',
    'Safety (S)',
    'Kicker (K)',
    'Punter (P)',
  ];

  const cardStyles = [
    'Modern',
    'Vintage',
    'Chrome',
    'Prizm',
    'Super Bowl Special',
    'Hall of Fame',
    'Rookie Card',
  ];

  return (
    <main className="min-h-screen bg-black text-white">
      <div className="max-w-3xl mx-auto px-4 py-12">
        <div className="space-y-6">
          <div className="text-center">
            <h1 className="text-4xl font-bold mb-4">Generate Your Football Trading Card</h1>
            <p className="text-gray-400">Create a unique digital football trading card using AI technology.</p>
          </div>

          <div className="bg-white/5 backdrop-blur-sm rounded-xl p-8 space-y-6">
            <div className="space-y-4">
              <div>
                <label htmlFor="playerName" className="block text-sm font-medium mb-2">
                  Player Name
                </label>
                <input
                  id="playerName"
                  type="text"
                  placeholder="Enter player name"
                  value={playerName}
                  onChange={(e) => setPlayerName(e.target.value)}
                  className="w-full px-4 py-2 bg-black/50 border border-white/10 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500"
                />
              </div>

              <div>
                <label htmlFor="team" className="block text-sm font-medium mb-2">
                  Team
                </label>
                <input
                  id="team"
                  type="text"
                  placeholder="Enter team name"
                  value={team}
                  onChange={(e) => setTeam(e.target.value)}
                  className="w-full px-4 py-2 bg-black/50 border border-white/10 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500"
                />
              </div>

              <div>
                <label htmlFor="position" className="block text-sm font-medium mb-2">
                  Position
                </label>
                <select
                  id="position"
                  value={position}
                  onChange={(e) => setPosition(e.target.value)}
                  className="w-full px-4 py-2 bg-black/50 border border-white/10 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500"
                >
                  <option value="">Select a position</option>
                  {positions.map((pos) => (
                    <option key={pos} value={pos}>
                      {pos}
                    </option>
                  ))}
                </select>
              </div>

              <div>
                <label htmlFor="season" className="block text-sm font-medium mb-2">
                  Season
                </label>
                <input
                  id="season"
                  type="text"
                  placeholder="Enter season (e.g., 2024-25)"
                  value={season}
                  onChange={(e) => setSeason(e.target.value)}
                  className="w-full px-4 py-2 bg-black/50 border border-white/10 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500"
                />
              </div>

              <div>
                <label htmlFor="jerseyNumber" className="block text-sm font-medium mb-2">
                  Jersey Number
                </label>
                <input
                  id="jerseyNumber"
                  type="text"
                  placeholder="Enter jersey number"
                  value={jerseyNumber}
                  onChange={(e) => setJerseyNumber(e.target.value)}
                  className="w-full px-4 py-2 bg-black/50 border border-white/10 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500"
                />
              </div>

              <div>
                <label htmlFor="achievement" className="block text-sm font-medium mb-2">
                  Achievement/Highlight
                </label>
                <input
                  id="achievement"
                  type="text"
                  placeholder="e.g., Super Bowl MVP, Pro Bowl Selection, etc."
                  value={achievement}
                  onChange={(e) => setAchievement(e.target.value)}
                  className="w-full px-4 py-2 bg-black/50 border border-white/10 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500"
                />
              </div>

              <div>
                <label htmlFor="cardStyle" className="block text-sm font-medium mb-2">
                  Card Style
                </label>
                <select
                  id="cardStyle"
                  value={cardStyle}
                  onChange={(e) => setCardStyle(e.target.value)}
                  className="w-full px-4 py-2 bg-black/50 border border-white/10 rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500"
                >
                  {cardStyles.map((style) => (
                    <option key={style} value={style.toLowerCase()}>
                      {style}
                    </option>
                  ))}
                </select>
              </div>
            </div>

            <button
              onClick={handleGenerateCard}
              className="w-full py-3 bg-gradient-to-r from-violet-500 to-fuchsia-500 rounded-lg font-semibold hover:opacity-90 transition-opacity"
            >
              Generate Card
            </button>

            {cardImage && (
              <div className="mt-8">
                <h2 className="text-xl font-semibold mb-4">Your Generated Card</h2>
                <div className="rounded-lg overflow-hidden">
                  <img src={cardImage} alt="Generated Trading Card" className="w-full" />
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </main>
  );
}
